("Taumatropo")]),_c('p',{staticClass:"mb-4"},[_vm._v("Peter Mark Roget descubrió el principio de la ‘persistencia de la visión’. En palabras simples, este principio plantea que los ojos retienen temporalmente lo que acaban de ver durante una décima de segundo antes de desaparecer, las imágenes se superponen en la retina y el cerebro las “enlaza” como una imagen continua. Este principio, que ha sido refutado a lo largo del tiempo, sugiere que percibimos la realidad como una secuencia de imágenes y por ende podemos calcular la velocidad y dirección de un objeto. ")]),_c('p',{staticClass:"mb-0"},[_vm._v("El taumatropo inventado por John Ayrton Paris (1824), consiste en un disco con dos imágenes diferentes en ambos lados y un trozo de cuerda a cada lado del disco. Al hacer girar rápidamente el disco, las caras se intercambian y produce una ilusión óptica donde se combinan las imágenes. ")])])]),_c('div',{staticClass:"row",attrs:{"titulo":"1832"}},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('figure',{staticClass:"mb-4"},[_c('img',{staticClass:"w-50 mx-auto",attrs:{"src":require("@/assets/curso/tema2/img07.jpg"),"alt":"historia imagén 6"}})]),_c('h3',{staticClass:"mb-2"},[_vm._v("Fenaquistoscopio")]),_c('p',{staticClass:"mb-0"},[_vm._v("El fenaquistoscopio inventado por Joseph Plateau y Simon von Stampfer (1832), está compuesto por dos discos conectados por un eje. El disco frontal tiene cortes o rendijas a lo largo del borde y el disco posterior tiene una secuencia de dibujos. Al hacer girar los discos y mirar a través de las rendijas, se tiene ilusión de movimiento.")])]),_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('figure',{staticClass:"mb-4"},[_c('img',{staticClass:"w-50 mx-auto",attrs:{"src":require("@/assets/curso/tema2/img08.jpg"),"alt":"historia imagén 7"}})]),_c('h3',{staticClass:"mb-2"},[_vm._v("Zootropo")]),_c('p',{staticClass:"mb-0"},[_vm._v("Este instrumento victoriano fue vendido como un juguete y consistía en un tambor circular con rendijas. 